    --white: hsl(0, 0%, 100%);
    --trs-duration: 300ms;
    --trs-timing: cubic-bezier(.46, .03, .52, .96);
    --brs-width: .35em;
    --loader-bgc: hsla(0, 0%, 0%, 0.8);
}

[data-theme="light"] {
    --bg-color: hsl(0, 0%, 94%);
    --ele-bg-color: var(--white);
    --txt-color: hsl(200, 15%, 8%);
    --ip-color: hsl(0, 0%, 52%);
    --bxs-color: hsl(0, 0%, 58%);
}

[data-theme="dark"] {
    --bg-color: hsl(207, 26%, 17%);
    --ele-bg-color: hsl(209, 23%, 22%);
    --txt-color: var(--white);
    --ip-color: var(--white);
    --bxs-color: hsl(210, 23%, 30%);
}

html {
    box-sizing: border-box;

}

*,
*::before,
*::after {
    margin: unset;
    padding: unset;
    box-sizing: inherit;
}

body {
    background-color: var(--bg-color);
    color: var(--txt-color);
    font-family: "Nunito Sans", system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    transition: var(--trs-duration) background-color var(--trs-timing), color var(--trs-duration) var(--trs-timing);
}

button,
input,
input::placeholder,
a {
    font-family: inherit;

}

li {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}

button {
    color: inherit;
    cursor: pointer;
    border: none;
    background-color: inherit;
}

img {
    max-width: 100%;
    max-height: 100%;
}


.loading_container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    inset: 0;
    background-color: var(--ele-bg-color);
    z-index: 100;
    height: 100dvh;

}


.loading_container.hidden {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0ms ease-in-out,
        opacity var(--trs-duration) var(--trs-timing);
}

.container {
    opacity: 0;
    visibility: hidden;
    transition: visibility 0ms ease-in-out,
        opacity var(--trs-duration) var(--trs-timing);
}

.container.visible {
    visibility: visible;
    opacity: 1;
}

.header,
.main,
.wrapper {
    padding: 1.75em 1.15em;

}

.header {
    display: grid;
    background-color: var(--ele-bg-color);
    grid-template-columns: 2fr 1fr;
    place-content: center;
}

.header>h1 {
    font-weight: 800;
    font-size: 1em;
}

.header img {
    width: 23px;

}

.header>button {
    text-transform: capitalize;
    display: flex;
    align-items: center;
    justify-content: end;
    gap: 0.45em;
    font-size: 0.85rem;
    font-weight: 600;
}
.wrapper {
    padding-bottom: 0;
}
.form>.search_container {
    background-color: var(--ele-bg-color);
    display: flex;
    color: var(--ip-color);
    align-items: center;
    justify-content: center;
    height: 51px;
    padding-inline: 1.55em;
    gap: 1.1em;
    max-width: 36em;
    border-radius: var(--brs-width);
    margin-bottom: 2em;
}

.form>.search_container svg {
    color: cadetblue;
    width: 30px;
    flex: 0 1 30px;
}

.form>.search_container:has(> input:focus) {
    outline: 2px solid cadetblue;
    box-shadow: 0px 0px 10px cadetblue;
    transition: all var(--trs-duration) var(--trs-timing);
}

.form>.search_container input {
    background-color: inherit;
    color: inherit;
    border: none;
    height: 60%;
    font-size: 0.9rem;
    width: 75%;
    flex: 1 1 100%;

}

.form>.search_container input:focus {
    outline: transparent;
}
.filter_wrapper{
    position: relative;
}
.filter_wrapper button,
.filter_wrapper button+div {
    font-size: .88rem;
    background-color: var(--ele-bg-color);
    width: 14em;
    border-radius: var(--brs-width);
    padding: 1.1em .65em 1.1em 1.35em;
}

.filter_wrapper button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
    margin-bottom: 0.5em;
}

.filter_wrapper button svg {
    width: 20px;
    rotate: 0deg;
    transition: rotate var(--trs-duration) var(--trs-timing);
}

.filter_wrapper button.expanded>svg {
    rotate: 180deg;
}

.filter_wrapper button+div {
    position: absolute;
    height: auto;
    max-height: 0px;
    max-width: 0px;
    overflow: hidden;
    visibility: hidden;
    opacity: 0;
    transition: max-height var(--trs-duration) var(--trs-timing) 40ms, visibility 0ms ease-in-out, opacity var(--trs-duration) var(--trs-timing) 40ms, max-width var(--trs-duration) var(--trs-timing);
}

.filter_wrapper button.expanded+div {
    visibility: visible;
    opacity: 1;
    max-height: 14em;
    max-width: 14em;
}

.filter_wrapper button+div a {
    display: block;
    margin-bottom: 0.65em;
    width: 100%;
}

.filter_wrapper button+div a:last-of-type {
    margin-bottom: 0;
}
.main {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(225px, 1fr));
    justify-content: center;
    align-items: center;
    gap: 3em;
}
.country_card{
    display: flex;
    flex-direction: column;
    width: 85%;
    max-height: fit-content;
    margin: 0 auto;
    background-color: var(--ele-bg-color);
    border-radius: var(--brs-width);
}
.country_card > section{
    padding: 1.35em;
}
.country_card>section h1{
    font-weight: 800;
    margin-bottom: 0.55em;
}
.country_card>section p {
    font-weight: 300; 
    text-transform: capitalize;
}
.country_card>section p > span{
    font-weight: 600;
}
.country_card > figure{
    order: -1;
    border-top-left-radius: var(--brs-width);
    border-top-right-radius: var(--brs-width);
}
.country_card>figure img {
    object-fit: contain;
    border-radius: inherit;
    height: fit-content;
    width: 100%;

}
@media only screen and (min-width: 800px) {

    .header,
    .main,
    .wrapper {
        padding: 1.5em 3.5em;

    }

    .header>h1 {
        font-size: 1.45rem;
    }

    .wrapper {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(20em, 1fr));

    }

    .wrapper>div {
        justify-self: right;
    }
